export const bitcoinService = {
    getRate,
    getCharts
}

async function getCharts(props) {
    const marketPrice = await _getMarketPrice()
    const tradeVolume = await _getTradeVolume()
    const avgBlockSize = await _getAvgBlockSize()
    return { marketPrice, tradeVolume, avgBlockSize }
}

async function getRate() {
    try {
        return rateData.USD.last
        // const res = await axios.get(RATE_DATA)
        // console.log(`res.data.USD`, res.data.USD)
        // return res.data.USD
    } catch (err) {
        console.log(`err`, err)
    }
}

async function _getMarketPrice() {
    return {
        title: `Market Price (USD)`,
        description: `The average USD market price across major bitcoin exchanges.`,
        posColor: '#ffa600',
        negColor: 'red',
        data: marketPrice.values.map(val => ({
            name: (new Date(val.x * 1000)).toLocaleDateString(),
            USD: val.y
        })), unit: 'USD'
    }
}

async function _getTradeVolume() {
    return {
        title: `Exchange Trade Volume (USD)`,
        description: `The total USD value of trading volume on major bitcoin exchanges.`,
        posColor: '00ffaa',
        negColor: 'red',
        data: tradeVolume.values.map(val => ({
            name: (new Date(val.x * 1000)).toLocaleDateString(),
            USD: val.y
        })),
        unit: 'USD'
    }
}
async function _getAvgBlockSize() {
    return {
        title: `Average Block Size (MB)`,
        description: `The average block size over the past 24 hours in megabytes.`,
        posColor: '6f00ff',
        negColor: 'red',
        data: avgBlockSize.values.map(val => ({
            name: (new Date(val.x * 1000)).toLocaleDateString(),
            MB: val.y
        })),
        unit: 'MB'
    }
}

